import { useState } from "react";
import styles from "./SobreProjeto.module.css";
import foto1 from "../../assets/img_util/CachorroCorrendo.jpg";
import foto2 from "../../assets/img_util/GatoDeitado.jpg";
import foto3 from "../../assets/img_util/CachorroDeitado.jpg";
import impacta from "../../assets/logo_grupo_impacta_azul-removebg-preview.png";

function SobreProjeto() {
  const [ativo, setAtivo] = useState("missao");
  return (
    <div className={styles.container}>
      <section className={styles.banner}>
        <img src={impacta} alt="Faculdade Impacta" />
      </section>

      <section className={styles.historia}>
        <h2>Nossa Histria</h2>
        <p>
          O Pet Resgate nasceu em 2025 com um propsito claro: conectar pessoas,
          ONGs e amantes de animais para transformar a realidade dos pets em
          situao de abandono. Acreditamos que, com tecnologia, empatia e
          colaborao,  possvel construir um futuro mais digno para os animais
          de rua. Somos uma equipe independente de estudantes e profissionais
          apaixonados pela causa animal e pela inovao. Criamos uma plataforma
          digital que facilita o resgate, acolhimento e adoo responsvel de
          animais, tornando o processo mais rpido, seguro e acessvel. Nosso
          aplicativo permite que qualquer pessoa registre um animal encontrado
          na rua, acione ONGs parceiras e acompanhe todo o processo at a
          adoo. Tambm oferecemos um espao para divulgao de histrias de
          sucesso, contedos educativos e ferramentas que ajudam a promover a
          conscientizao e o cuidado responsvel com os pets. Mais do que um
          aplicativo, o Pet Resgate  um movimento. Um convite para unir foras,
          dar voz aos que no tm e criar novas possibilidades para milhares de
          vidas. Se voc acredita nesse propsito, vem com a gente!
        </p>
      </section>

      <section className={styles.mvv}>
        <div className={styles.botoes}>
          <button
            className={ativo === "missao" ? styles.ativo : ""}
            onClick={() => setAtivo("missao")}
          >
            Misso
          </button>
          <button
            className={ativo === "visao" ? styles.ativo : ""}
            onClick={() => setAtivo("visao")}
          >
            Viso
          </button>
          <button
            className={ativo === "valores" ? styles.ativo : ""}
            onClick={() => setAtivo("valores")}
          >
            Valores
          </button>
        </div>

        <div className={styles.mvvItem}>
          {ativo === "missao" && (
            <p>
              Nossa misso  transformar a realidade dos animais em situao de
              abandono por meio da tecnologia, empatia e colaborao. O Pet
              Resgate existe para facilitar o resgate, o acolhimento e a adoo
              responsvel, conectando pessoas dispostas a ajudar com ONGs e pets
              que precisam de um novo comeo. Queremos ser a ponte entre quem
              encontra um animal na rua e aqueles que podem oferecer amor,
              cuidado e um lar de verdade. Trabalhamos para tornar esse processo
              mais gil, humano e acessvel, sempre com o bem-estar animal como
              prioridade.
            </p>
          )}
          {ativo === "visao" && (
            <p>
              Sonhamos com um futuro em que nenhum animal precise viver nas
              ruas, abandonado ou invisvel. Queremos ser reconhecidos como a
              principal plataforma de resgate e adoo do Brasil, promovendo
              solues inovadoras que unem tecnologia e causa social. Nossa
              viso  construir uma rede forte e engajada, onde cidados, ONGs e
              voluntrios possam colaborar com facilidade e transparncia.
              Acreditamos que, juntos, podemos criar um impacto real e
              duradouro, oferecendo a milhares de animais uma nova chance de
              viver com dignidade, segurana e afeto.
            </p>
          )}
          {ativo === "valores" && (
            <p>
              Empatia: Colocar o bem-estar dos animais sempre em primeiro lugar.
              Inovao: Usar a tecnologia como ponte para solues eficazes e
              acessveis. Colaborao: Unir foras com ONGs, cidados e
              voluntrios em prol de um bem comum. Transparncia: Agir com
              clareza e responsabilidade em cada etapa do processo. Educao:
              Informar e conscientizar para transformar atitudes e promover o
              cuidado responsvel.
            </p>
          )}
        </div>
      </section>
      <section className={styles.galeria}>
        <img src={foto1} alt="Pet 1" />
        <img src={foto2} alt="Pet 2" />
        <img src={foto3} alt="Pet 3" />
      </section>
    </div>
  );
}

export default SobreProjeto;
